/* --- SCSS Variables (Import or Define) --- */
// These variables should ideally be in a central _variables.scss file and imported.
// For this example, they are redefined to match your design language.

// Color Palette
$primary-color: #0d6efd;
$secondary-color: #6c757d;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

// Layout & UI Colors
$app-background-color: #f8f9fa;
$container-background-color: #ffffff; // Using a clean white for the main container background
$text-color-primary: #212529;
$text-color-secondary: #495057;
$border-color: #dee2e6; // A standard, light border color
$sidebar-background-color: #DDEBF3; // From your navbar styles

// Layout & Spacing
$border-radius: 8px;
$base-padding: 1.5rem; // 24px, consistent with grid-gap
$grid-gap: 1.5rem;
$base-margin: 1rem;
$base-shadow: 0 5px 15px rgba(0, 0, 0, 0.07);

/* --- Main Content Area --- */
// Assuming .main-content is the flex-grow container from your layout
.main-content {
    flex-grow: 1;
    padding: $grid-gap;
    overflow-y: auto;
}

/* --- Users Page Container --- */
.users-container {
    background-color: $container-background-color;
    border-radius: $border-radius;
    padding: $base-padding;
    border: 1px solid transparentize($border-color, 0.5);
}

/* --- Page Header --- */
.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $grid-gap;
    padding-bottom: $base-padding;
    border-bottom: 1px solid $border-color;

    .page-title {
        font-size: 1.75rem;
        font-weight: 600;
        color: darken($text-color-secondary, 20%);
    }
}

/* --- Buttons (Generic styles, should be in a universal file) --- */
.btn {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.6rem 1.2rem;
    font-size: 0.95rem;
    font-weight: 500;
    border-radius: $border-radius;
    border: 1px solid transparent;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    white-space: nowrap;

    .material-symbols-outlined {
        font-size: 1.25rem;
    }

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 10px rgba(0,0,0,0.1);
    }
}

.btn-primary {
    background-color: $primary-color;
    color: #fff;
    &:hover { background-color: darken($primary-color, 8%); }
}

.btn-secondary {
    background-color: $secondary-color;
    color: #fff;
    &:hover { background-color: darken($secondary-color, 8%); }
}

.btn-danger {
    background-color: $danger-color;
    color: #fff;
    &:hover { background-color: darken($danger-color, 8%); }
}

.btn-success {
    background-color: $success-color;
    color: #fff;
    &:hover { background-color: darken($success-color, 8%); }
}


/* --- Feedback Messages --- */
.message {
    padding: $base-margin;
    margin-bottom: $grid-gap;
    border-radius: $border-radius;
    border: 1px solid;
    font-weight: 500;

    &.success {
        background-color: lighten($success-color, 40%);
        border-color: darken(lighten($success-color, 40%), 10%);
        color: darken($success-color, 10%);
    }

    &.error {
        background-color: lighten($danger-color, 30%);
        border-color: darken(lighten($danger-color, 30%), 10%);
        color: darken($danger-color, 10%);
    }
}


/* --- Users Table Styling --- */
.users-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: $grid-gap;
    font-size: 0.9rem;

    th, td {
        padding: 0.9rem 1rem;
        text-align: left;
        vertical-align: middle;
        border-bottom: 1px solid $border-color;
    }

    thead th {
        background-color: lighten($app-background-color, 2%);
        font-weight: 600;
        color: $text-color-secondary;
        border-bottom-width: 2px;
        border-top: 1px solid $border-color;
    }

    tbody tr {
        transition: background-color 0.15s ease-in-out;
        &:hover {
            background-color: lighten($sidebar-background-color, 5%);
        }
    }

    td {
        color: $text-color-primary;
    }

    // Column-specific alignments
    th:first-child, td:first-child { // ID
      width: 5%;
    }
    th:last-child, td:last-child { // Actions
      width: 15%;
      text-align: right;
    }
}

.actions {
    display: flex;
    gap: 0.5rem;
    justify-content: flex-end;

    .btn { // Smaller buttons for actions
        padding: 0.35rem 0.6rem;
        .material-symbols-outlined { font-size: 1.1rem; }
    }
}

/* --- Badges for Role & Status --- */
.role-badge, .status-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.35rem;
    padding: 0.25rem 0.6rem;
    border-radius: 1rem; // Pill shape
    font-weight: 500;
    font-size: 0.8rem;
    white-space: nowrap;
}

.role-badge {
    &.role-admin {
        background-color: lighten($warning-color, 30%);
        color: darken($warning-color, 25%);
    }
    &.role-user {
        background-color: lighten($secondary-color, 35%);
        color: $secondary-color;
    }
}

.status-badge {
    &.status-active {
        background-color: lighten($success-color, 45%);
        color: darken($success-color, 15%);
    }
    &.status-inactive {
        background-color: lighten($danger-color, 35%);
        color: darken($danger-color, 15%);
    }
    .material-symbols-outlined {
        font-size: 1em; // Inherit size from parent badge
    }
}


/* --- Empty State --- */
.empty-state {
    text-align: center;
    padding: $grid-gap * 2;
    border: 2px dashed $border-color;
    border-radius: $border-radius;
    margin-top: $grid-gap;

    .material-symbols-outlined {
        font-size: 4rem;
        color: lighten($text-color-secondary, 30%);
    }
    h3 {
        margin-top: $base-margin;
        color: $text-color-secondary;
        font-weight: 600;
    }
    p {
        color: lighten($text-color-secondary, 10%);
        max-width: 300px;
        margin: 0.5rem auto 0;
    }
}


/* --- Modal Styling --- */
.modal {
    display: none; /* This now correctly hides the modal by default */
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    
    /* The flex properties are removed from here */
}

/* We must ensure the flex properties are applied when the modal is shown by JS */
/* The JS uses style="display: block", but for centering we should target it when it's not 'none' */
.modal[style*="display: block"] {
    display: flex !important; /* Use flex for centering when JS makes it visible */
    align-items: center;
    justify-content: center;
}

.modal-content {
    background-color: $container-background-color;
    margin: auto;
    border: 1px solid #888;
    width: 90%;
    max-width: 500px;
    border-radius: $border-radius;
    box-shadow: 0 10px 30px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    animation: slide-down 0.3s ease-out;
}

@keyframes slide-down {
    from {
        transform: translateY(-30px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}


.modal-header {
    padding: $base-margin $base-padding;
    border-bottom: 1px solid $border-color;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .modal-title {
        font-size: 1.25rem;
        font-weight: 600;
        color: $text-color-primary;
    }
    .close {
        color: $secondary-color;
        font-size: 2rem;
        font-weight: bold;
        cursor: pointer;
        background: none;
        border: none;
        line-height: 1;
        &:hover, &:focus {
            color: $danger-color;
        }
    }
}

// Apply consistent padding and no border to form itself
#userForm, .modal-content > p {
    padding: $base-padding;
}

.form-group {
    margin-bottom: $base-margin;
}

.form-label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: $text-color-secondary;
    font-size: 0.9rem;
}

.form-control {
    display: block;
    width: 100%;
    padding: 0.75rem 1rem;
    font-size: 1rem;
    background-color: #fff;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

    &:focus {
        border-color: $primary-color;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($primary-color, 0.25);
    }
}

select.form-control {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right 1rem center;
    background-size: 16px 12px;
}

.form-check {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 0;
}

.form-check-input {
    width: 1.25em;
    height: 1.25em;
    cursor: pointer;
    accent-color: $primary-color;
}

.modal-footer {
    padding: $base-margin $base-padding;
    border-top: 1px solid $border-color;
    background-color: lighten($app-background-color, 2%);
    display: flex;
    justify-content: flex-end;
    gap: 0.75rem;
    border-radius: 0 0 $border-radius $border-radius;
}

/* --- Delete Confirmation Modal --- */
#deleteModal .modal-content {
    p {
        color: $text-color-secondary;
        line-height: 1.5;
        strong {
            color: $text-color-primary;
        }
    }
    p:last-of-type {
        font-weight: 500;
        color: darken($danger-color, 10%);
    }
}